.rc-color-picker {
  position: absolute;
}
.color-picker-panel {
  border-radius: 4px;
  box-shadow: 0 1px 5px #ccc;
  border: 1px solid #ccc;
  padding: 8px;
  background-color: #ffffff;
  overflow-y: visible;
  z-index: 1001;
  width: auto;
  position: absolute;
  padding: 5px;
  font-size: 12px;
}
.color-picker-panel .color-picker-first-row,
.color-picker-panel .color-picker-last-row,
.color-picker-panel .color-picker-compactrow {
  width: auto;
}
.color-picker-panel .color-picker-first-row,
.color-picker-panel .color-picker-last-row {
  margin: 6px 0;
}
.color-picker-panel .color-picker-color-auto a canvas {
  margin-bottom: -3px;
  margin-right: 5px;
}
.color-picker-panel ul,
.color-picker-panel li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.color-picker-panel li {
  display: inline-block;
}
.color-picker-panel .color-picker-cell {
  padding: 0px 0px 0px 2px;
}
.color-picker-panel .color-picker-cell a {
  cursor: pointer;
  outline: none;
}
.color-picker-panel .color-picker-cell a:focus canvas,
.color-picker-panel .color-picker-cell a:hover canvas {
  border-color: #0095cc;
}
.color-picker-panel .color-picker-cell .color-picker-celldiv {
  width: 11px;
  height: 11px;
  border-style: solid;
  border-color: #e1e1e1;
  padding: 1px;
  border-width: 1px;
  outline: none;
}



